
import Vue from "vue";
import Component from "vue-class-component";
import LoadingItem from "../loading/LoadingItem.vue";
import { formatMoney } from "../../helpers/rules";
import { Product } from "@/models";

@Component({
  components: {
    LoadingItem,
  },
})
export default class ProductDetail extends Vue {
  public productName: string = this.$route.params.productName;
  public productId: string = this.$route.params.productId;
  public loading: boolean = true;
  public formatMoney(money: number) {
    return formatMoney(money);
  }
  public get getProducts() {
    this.$store.dispatch("getDbProduct");
    if (this.$store.getters.slideProducts.length > 0) this.loading = false;
    return this.$store.state.products.filter(
      (val: Product) => val.id === this.productId
    );
  }
  public showType(type: string) {
    switch (type) {
      case "phone":
        return "Cell Phone";
      case "laptop":
        return "Laptop";
      case "headphone":
        return "Headphone";
      case "watch":
        return "Watch";
      case "charger":
        return "Charger";
      case "phoneCase":
        return "Phone Case";
      case "ipad":
        return "Ipad";
      case "mouse":
        return "Mouse";
      case "keyboard":
        return "Keyboard";
      case "screen":
        return "Screen";
      case "imac":
        return "iMac";
      default:
        return "null";
    }
  }
  mounted() {
    window.scrollTo(0, 0);
  }
}
